.dropdown{position:relative}.dropdown__layer{position:fixed;background:rgba(0,0,0,0);width:100vw;height:100vh;top:0;bottom:0;left:0;right:0;z-index:99;visibility:hidden;opacity:0}.dropdown_static .increment-box{display:-webkit-box;display:-ms-flexbox;display:flex;visibility:visible;opacity:1;position:relative;z-index:0}.dropdown_static .dropdown__details[open] .dropdown__summary{border:1px solid rgba(31,32,65,.5);border-radius:4px 4px 0 0}.dropdown_expanded .dropdown__layer{visibility:visible;opacity:1}.dropdown_expanded .dropdown__body{visibility:visible;opacity:1}.dropdown_expanded .dropdown__body,.dropdown_expanded .dropdown__summary{-webkit-transition:all .6s ease;-o-transition:all ease .6s;transition:all .6s ease}.dropdown_expanded .dropdown__summary{border:1px solid rgba(31,32,65,.5);border-radius:4px 4px 0 0}.dropdown_filter-date-dropdown .dropdown__summary{border-radius:4px;border:1px solid rgba(31,32,65,.25);margin:0 0 5px}.dropdown_filter-date-dropdown .dropdown__summary:hover{border:1px solid rgba(31,32,65,.25)}.dropdown__heading{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;margin:0 0 5px}.dropdown__subtitle,.dropdown__title{text-transform:uppercase}.dropdown__subtitle{font-weight:400;color:rgba(31,32,65,.5)}.dropdown__details{position:relative}.dropdown__summary{display:-webkit-box;display:-ms-flexbox;display:flex;position:relative;background:#fff;border:1px solid rgba(31,32,65,.25);border-radius:4px;padding:12px 15px 8px;cursor:pointer;-webkit-transition:all .6s ease;-o-transition:all ease .6s;transition:all .6s ease}.dropdown__summary:hover{border:1px solid rgba(31,32,65,.5)}.dropdown__summary:after{content:"expand_more";font-family:Material Icons;width:44px;height:44px;font-size:24px;line-height:24px;color:rgba(31,32,65,.75);position:absolute;right:0;top:0;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.dropdown__text{width:160px;overflow:hidden;-o-text-overflow:ellipsis;text-overflow:ellipsis;white-space:nowrap;color:rgba(31,32,65,.75);pointer-events:none}.dropdown__body{position:absolute;visibility:hidden;opacity:0;z-index:2;-webkit-transition:all .6s ease;-o-transition:all ease .6s;transition:all .6s ease;width:100%;z-index:100}.dropdown__inputs{display:grid;grid-template-columns:repeat(auto-fill,minmax(149px,auto));grid-column-gap:20px;margin:0 0 5px;grid-row-gap:20px}.dropdown__input{border:1px solid rgba(31,32,65,.25);border-radius:4px;cursor:pointer;position:relative;padding:12px 15px 8px;max-width:100%;font-size:14px;line-height:24px;color:rgba(31,32,65,.75);width:100%}.dropdown__icon{position:absolute;font-size:24px;line-height:24px;color:rgba(31,32,65,.5);right:12px;top:31px;pointer-events:none}