.main_cards{background:#f4f4f6;padding:150px 140px 140px;display:grid;grid-template-columns:minmax(300px,380px) minmax(300px,380px) minmax(300px,320px);grid-column-gap:40px}.cards__item:not(:last-child){margin:0 0 40px}.form-wrapper{padding:40px 30px 30px;background-color:#fff;border:1px solid rgba(0,0,0,.12);-webkit-box-shadow:0 0 25px rgba(0,0,0,.2);box-shadow:0 0 25px rgba(0,0,0,.2);border-radius:4px;width:100%;max-width:380px}.form__header{margin:0 0 20px}.form__title{font-family:Quicksand,Open Sans,sans-serif;font-weight:700;font-size:24px;line-height:30px}.form__content-item:not(:last-child){margin:0 0 20px}.form__content-item_10:not(:last-child){margin:0 0 10px}.form__content-item_30:not(:last-child){margin:0 0 30px}.form-footer{padding:30px 0 0;display:-webkit-box;display:-ms-flexbox;display:flex;font-size:14px;line-height:18px;color:#000;-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between}.form-footer__button{min-width:99px}.booking-room{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.booking-room__number{font-family:Quicksand,Open Sans,sans-serif;font-weight:700;font-size:24px;line-height:30px;margin:0 5px 0 0}.booking-room__number_small{font-weight:700;font-size:14px;line-height:17px;margin:0}.booking-room__lux{font-weight:700;font-size:12px;line-height:15px;text-transform:uppercase;color:#bc9cff}.booking-room__price{font-weight:700;font-size:14px;line-height:18px;color:rgba(31,32,65,.5)}.booking-room__price-additional{font-weight:400;font-size:12px;line-height:18px}.calculations{color:rgba(31,32,65,.75)}.calculations__body{margin:0 0 30px}.calculations__item{display:grid;grid-template-columns:minmax(auto,214px) 20px auto;grid-template-rows:minmax(auto,24px);grid-column-gap:5px;grid-row-gap:5px;-webkit-box-align:baseline;-ms-flex-align:baseline;align-items:baseline}.calculations__item:not(:last-child){margin:0 0 10px}.calculations__text{line-height:18px}.calculations__icon{display:-webkit-box;display:-ms-flexbox;display:flex;border:1px solid rgba(31,32,65,.25);width:20px;height:20px;border-radius:50%;font-weight:700;font-size:12px;color:rgba(31,32,65,.25);-webkit-box-align:center;-ms-flex-align:center;align-items:center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;cursor:pointer}.calculations__number{text-align:right}.calculations__result{font-weight:700;font-size:19px;line-height:24px;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;font-family:Quicksand,Open Sans,sans-serif}.calculations__result-text{margin:0 5px 0 0}.calculations__result-dots{border-bottom:1px dotted rgba(31,32,65,.25);-webkit-box-flex:1;-ms-flex:1 0;flex:1 0;margin:0 5px 0 0;height:19px}